















import {mapState} from 'vuex'
export default {
	data() {
		return {
			current: 1,
			size: 10
		};
	},
	computed: {
		...mapState(['mycontract', 'mycontracttotal'])
	},
	onLoad() {
		// 获取我的成交列表
		this.optionCarload();
	},
	methods: {
		optionCarload(load) {
			let option = {
				current: this.current,
				size: this.size
			}
			// 获取联盟车辆列表
			this.$store.dispatch('getMycontract', { option, load });
		},
	},
	// 下拉刷新事件
	onPullDownRefresh() {
		this.current = 1;
		this.optionCarload(true);
	},
	// 上拉加载更多事件
	onReachBottom() {
		if(this.mycontract.length == this.mycontracttotal)
			return;
			
		this.current++;
		console.log(this.current);
		this.optionCarload();
	}
}



